<template>
    <section class="HeroSection">
        <div class="HeroWrapper container">
            <div class="HeroBody">
                <h1>{{title}}</h1>
            </div>
        </div>
    </section>
</template>
<script>
    export default {
        name: "HeroComponent",
        props: ['title', 'img', 'position'],
        mounted () {
            let bacground = document.getElementsByClassName("HeroSection")
            bacground[0].style.backgroundImage = `url(${require("@/assets/images/windowImage.png")}), url(${require(`@/assets/images/${this.img}`)}`
            bacground[0].style.backgroundPosition = `center, top ${this.position}% center`;
        }
    }
</script>

<style scoped lang="scss">
    .HeroSection {
        background-repeat: no-repeat;
        background-size: cover, 2200px;
        .HeroWrapper {
            .HeroBody {
                padding: 130px 0;

                h1 {
                    font-size: 40px;
                    text-align: center;
                    color: #fff;
                    font-weight: 500;
                }
            }
        }
    }
</style>